.pop{
  width: 100%;
  height: 100vh;
  background-color: rgba(0,0,0,0.9);
  padding: 5vw;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 5;

  .con{
    width: 100%;
    height: 100%;
    position: relative;

    video, iframe{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    img{
      width: 100%;
      height: 100%;
      object-fit: contain;
    }

    .close{
      position: absolute;
      font-size: $fontM;
      color: orange;
      font-family: 'arial';
      top: -40px;
      right: 0;
      cursor: pointer;
    }
  }
}